/* all menus */
.menu,
.menubar {
	list-style: none;
	padding-left: 0;
	border-radius: $menu-border-radius;
	display: inline-block;

	&.wide {
		width: 100%;
	}

	li {
		a {
			color: $menu-color;
			background: $menu-bg;
			display: block;
			padding: $menu-anchor-padding;
			border: $menu-border;

			&:hover, &.active {
				color: $menu-hover-color;
				background: $menu-hover-bg;
				border-color: darken($menu-hover-bg, 2%);
			}
		}
	}
}

/* vertical menu */
.menu {

	li {
		display: block;
		width: 100%;

		a {
			border-top: 0;
		}
	}

	// first list item
	li:first-child {

		a {
			border-top: $menu-border;
			border-top-left-radius: $menu-border-radius;
			border-top-right-radius: $menu-border-radius;
		}
	}
	// last list item
	li:last-child {
		a {
			border-bottom-left-radius: $menu-border-radius;
			border-bottom-right-radius: $menu-border-radius;
		}
	}

	&.wide {

		li {
			width: 100%;

			a {
				display: block;
			}
		}
	}
}

/* horizontal menu */
.menubar {

	li {
		display: block;
		float: left;

		a {
			border-left: 0;
		}
	}

	// first list item
	li:first-child {

		a {
			border-left: $menu-border;
			border-top-left-radius: $menu-border-radius;
			border-bottom-left-radius: $menu-border-radius;
		}
	}
	// last list item
	li:last-child {
		a {
			border-top-right-radius: $menu-border-radius;
			border-bottom-right-radius: $menu-border-radius;
		}
	}

	&.wide {
		display: table;
		width: 100%;

		li {
			display: table-cell;
			float: none;

			a {
				display: block;
				text-align: center;
			}
		}
	}
}