// variables
$max-width: 70rem;

// brand colors
$primary: #003D6B;
$accent1: #607C8C;
$accent2: #7A2638;
$text: #000000;

$visual-grid: false;

// navbar 
$nav-background: $primary;

// default buttons
$buttons: 	(default #cccccc),
			(secondary #2ecc71),
			(primary $primary),
			(danger #e74c3c);

$btn-color: #fff;

// import crafted by kh
@import '../crafted/src/scss/crafted';

// ccr imports
@import 'elements';
@import 'header';
@import 'navigation';
@import 'showcase';
@import 'typography';
@import 'modules';
@import 'colophon';
@import 'arrow-boxes';

/* default classes */
.container {
	@include outer-container();
	padding: 0 1.5rem;
}
.breakout {
	width: 100vw;
	overflow: hidden;
}
.row {
	@include row();
}
.one-half {
	@include media ($tablet) {

	}
	@include media($desktop) {
		@include span-columns(6);		
	}
}
.one-third {
	@include media ($tablet) {
		
	}
	@include media($desktop) {
		@include span-columns(4);		
	}
}
.two-thirds {
	@include media ($tablet) {
		
	}
	@include media($desktop) {
		@include span-columns(8);		
	}
}
.full-width {
	@include span-columns(12);
}

/* html5 <main> element */
main {
	padding-bottom: 2rem;
}

/* html5 article.main element */
.main {
	@include span-columns(2 of 3);
}

/* html5 .sidebar element */
.sidebar {
	@include span-columns(1 of 3);
}

/* html5 .crafted-by-kh branding elements for the developer */
.crafted-by-kh {
	padding: 1rem;
	background: #222;
	color: #777;

	@include media($tablet) {
		text-align: right;
	}

	a {
		color: #aaa;

		&:hover {
			color: #ccc;
		}
	}

	a.kh {
		background: #444;
		color: #ccc;
		display: inline-block;
		padding: ms(-3) ms(-2);

		&:hover {
			background: #555;
		}
	}
}

