table {
	width: 100%;
	margin: ms(1) 0;
	border-collapse: collapse;
	border-spacing: 0;
	border: $table-border-color;

	td, th {
		border-top: $table-row-border-color;
		padding: $table-cell-padding;
	}

	th {
		background: $table-header-bg;
		color: $table-header-color;
		font-weight: normal;
		text-align: $table-td-align;
	}

	tbody {

		tr:nth-child(even) {
			td {
				background-color: $table-even-row-bg;
			}
		}
	}

	@media screen and (max-width: 600px) {

		thead, tfoot {
			display: none;
		}

		tbody {

			tr:nth-of-type(2n) {
				background-color: inherit;
			}
			tr { 
				td:first-child {
					background: $table-header-bg;
					color: $table-header-color;
				}
			}


			td {
				display: block;
				width: 100%;
				text-align: $table-td-mobile-align;
			}
			td:before { 
			    content: attr(data-th); 
			    display: block;
			    text-align: $table-td-mobile-align;  
			}
		}
	}
}