/* html .nav-mobile */
.nav-mobile {
  position: absolute;
  top: 0;
  right: 0;
  background: $nav-background;
  height: $nav-height;
  width: $nav-height;

  @include media($tablet) {
    display: none;
  }

  a {
    position: absolute;
    left: 18px;
    top: 22px;
    cursor: pointer;
    padding: 10px 35px 16px 0px;
    span,
    span:before,
    span:after {
      cursor: pointer;
      border-radius: 1px;
      height: 5px;
      width: 35px;
      background: $nav-font-color;
      position: absolute;
      display: block;
      content: '';
      transition: all $nav-toggle-speed ease-in-out;
    }
    span:before {
      top: -10px;
    }
    span:after {
      bottom: -10px;
    }
    &.active span {
      background-color: transparent;
      &:before,
      &:after {
        top: 0;
      }
      &:before {
        transform: rotate(45deg);
      }
      &:after {
        transform: rotate(-45deg);
      }
    }
  }
}

/* html .nav-menu + .nav-dropdowns */
.nav-menu {
  background: $nav-background;

  ul {
    list-style: none; 
    margin: 0;
    padding: 0;
    display: none;
    
    @include media($tablet) {
      @include outer-container();
      display: flex;
      justify-content: space-between;
    }

    li {
      text-align: center;

      a {
        color: $nav-font-color;
        display: block;
        padding: ms(0);

        @include media($tablet) {
          padding: ms(1) 3.6em;          
        }
        width: 100%;

        &:hover,
        &.active {
          background: $link-hover-background;
        }

        & + li.dropdown {
          background: $nav-dropdowns-bg;
          padding: ms(1) 0;
        }
      }


    } 
  }
}
.nav-dropdowns {
  padding: 0;
  margin: 0;
  background: $nav-dropdowns-bg;
  position: relative;
  width: 100%;
  z-index: 99;
  left: 0;

  ul {
    list-style: none;
    padding-left: none;
    margin: 0;
  }

  .dropdown {
    @include outer-container();
    display: none;
    padding: ms(1) 0;
  } 
}

