
html {
  @include typi-base();
  -webkit-font-smoothing: antialiased;
}

h1 { @include typi($h1-map) }
h2 { @include typi($h2-map) }
h3 { @include typi($h3-map) }
h4 { @include typi($h4-map) }
h5 { @include typi($h5-map) }
h6 { @include typi($h6-map) }

/* paragraph styles */
small {
  line-height: ms(0);
}

/* Tables */
table {
  margin-top: 1.5rem;
  border-spacing: 0px;
  border-collapse: collapse;
}
table td,
table th {
  padding: 0;
  line-height: 33px;
}
/* Code blocks */
code {
  vertical-align: bottom;
}
/* Leading paragraph text */
.lead {
  font-size: 1.414rem;
}
/* Hug the block above you */
.hug {
  margin-top: 0;
}

/* Anchor Styles */
a {
  text-decoration: none;
  color: $anchor;
}

/* Blockquote Styles */
blockquote {
  background: #f9f9f9;
  border-left: 10px solid #ccc;
  margin: 1.5em 10px;
  padding: 0.5em 10px;
  quotes: "\201C""\201D""\2018""\2019";
  overflow: hidden;
}
blockquote:before {
  color: #ccc;
  content: open-quote;
  font-size: 4em;
  line-height: 0.1em;
  margin-right: 0.25em;
  vertical-align: -0.4em;
}
blockquote p {
  display: inline;
}
blockquote cite {
  float: right;
  padding-top: 1.5rem;

  &::before {
    content: "\2014 ";
  }
}

dt {
   text-decoration: underline;
}
dd {
  margin: 0;
  padding: 0 0 1.618rem 0;
}

/**
 * global alignment styles
 */
.right {
  text-align: right;
}
.left {
  text-align: left;
}
.center {
  text-align: center;
}