/**
 * html .btn element
 */
[class^="btn"] {
	@include border-radius($radius);
	border: 0;
	color: $btn-color;
	padding: 0 1.168em;
	@include typi($btn-sm);
}

// small button
@mixin button-sm() {
	.#{$btn-class-sm} {
		@include typi($btn-sm);
	}
}

// medium button
@mixin button-md() {
	.#{$btn-class-md} {
  		@include typi($btn-md);
	}
}

// large button
@mixin button-lg() {
	.#{$btn-class-lg} {
  		@include typi($btn-lg);
	}
}

// button size classes
@include button-sm();
@include button-md();
@include button-lg();

// get each button
@each $button in $buttons {

	.btn-#{nth($button,1)} {
		background: nth($button,2);

		&:hover {
	  		background:lighten(nth($button,2),8%);
	  		transition: all 0.15s ease;
		}
		&:active {
	  		background:lighten(nth($button,2),12%);
		}
	}

}
