/**
 * form element
 */
form {

	div, 
	fieldset {
		@extend %border-box;
	}

	fieldset {
		border: $fieldset-border;
		background: $fieldset-bg;
		color: $fieldset-color;
		margin: $fieldset-margin;
		padding: $fieldset-padding;

		legend {
			width: 100%;
			font-size: ms(1);
			padding: ms(0) 0;
			display: block;
			text-align: center;
		}
	}

	/* labels */
	label {
		@extend %border-box;
		display: inline-block;
		padding: 1em 0;
	}

	.inline {
		white-space: nowrap;

		label:first-child {
			width: 20%;
			vertical-align: top;
		}

		input[type="text"],
		input[type="number"],
		input[type="email"], 
		input[type="password"], 
		select,
		textarea {
			vertical-align: top;
			width: 79%;
		}
	}

	/* text releted inputs */
	input[type="text"],
	input[type="email"],
	input[type="password"],
	input[type="number"],
	textarea {
		@extend %border-box;

		width: 100%;
		border: 1px solid $form-element-border;
		background: $form-element-background;
		padding: 1em 0.75em;
	}

	input[type="text"]:focus, input[type="text"]:hover,
	input[type="email"]:focus, input[type="email"]:hover,
	input[type="password"]:focus, input[type="password"]:hover,
	input[type="number"]:focus, input[type="number"]:hover,
	textarea:focus, textarea:hover {
		outline: 0;
		background: darken($form-element-background, 2%);
		border: 1px solid darken($form-element-border, 2%);
		transition: all 0.3s ease;
	}

	/* submit button */
	input[type="submit"] {
		border: 1px solid $form-element-border;
		border-radius: 0.5rem;
		background: $form-element-background;
		padding: 1em 1.75em;
	}
	input[type="submit"]:focus, input[type="submit"]:hover {
		outline: 0;
		background: $form-submit-hbg;
		color: $form-submit-hc;
		border: 1px solid darken($form-submit-hbg, 2%);
		transition: all 0.3s ease;
	}

	/* disabled inputs */
	input:disabled,
	input:disabled:hover {
		color: #aaa;
		background-color: $form-element-background;
		border: 1px solid darken($form-element-background, 2%);
	}

	/* textarea */
	textarea {

	}

	/* radios and checkboxes */
	input[type="checkbox"],
	input[type="radio"] {
	    display: none;
	}
	input[type="checkbox"] + label,
	input[type="radio"] + label {
		cursor: pointer;
		padding: 0.7em 0;
		margin-right: 1em;
	}

	input[type="checkbox"] + label:before,
	input[type="radio"] + label:before {
		content: "\00a0";
		border: 1px solid $form-element-border;
		background: $form-element-background;
		display:inline-block;
		width: 1.5em;
		line-height: 1.5em;
		margin-right: 0.5em;
	    vertical-align: middle;
	    padding: 0;
	    text-align: center;
	}

	input[type="checkbox"] + label:before {
		@include border-radius($radius);
	}
	input[type="radio"] + label:before {
		@include border-radius(50%);
	}

	input[type="checkbox"]:checked + label:before {
		content: "\2713";
	}

	input[type="radio"]:checked + label:before {
		content: "\25CF";
	}

	input[type="radio"]:checked + label:before,
	input[type="radio"]:hover + label:before,
	input[type="checkbox"]:checked + label:before,
	input[type="checkbox"]:hover + label:before {
		background: darken($form-element-background, 2%);
		border: 1px solid darken($form-element-border, 2%);
	}

	/* select field */
	select {
		@extend %border-box;
		height: 3em;
		border: 1px solid $form-element-border;
		border-radius: 0.5rem;
		background: $form-element-background;
		padding: 0em 0.75em;
		text-indent: 0.5em;
	    cursor:pointer;
	    width: 100%;
	    background-repeat: no-repeat;
	    background-image: url(data:image/png;base64,);
	    background-position: center right; 

	   -webkit-appearance:none;
	   -moz-appearance:none;
	    appearance:none;

		&:hover {
			background-color: darken($form-element-background, 2%);
			border: 1px solid darken($form-element-border, 2%);
		}
	}
	select::-ms-expand { /* for IE 11 */
	    display: none;
	}

}
