// bourbon neat variables
$disable-warnings: false !default;
$column: 4.2358em !default;
$gutter: 1.618em !default;
$grid-columns: 12 !default;
$max-width: 1200px !default;
$border-box-sizing: true !default;
$default-feature: min-width;
$default-layout-direction: LTR !default;
$visual-grid: false !default;
$visual-grid-color: #eee !default;
$visual-grid-opacity: 0.4 !default;

// typography elements 
$anchor: #3498db !default;

// grid variables 
$wrapper: 75em !default;
$gutter: 0.5rem !default;
$row-padding: 0.5rem !default;
$grid: 5 !default;

// media query breakpoints
$mq-sm: 35.5rem !default;
$mq-md: 48rem !default;
$mq-lg: 64rem !default;
$mq-xl: 80rem !default;

// setup modular scale
$ms-base: 1em !default;
$ms-ratio: $golden !default;

// typography
$fs-base: 16px !default;
$fs-lg: 16px !default;
$fs-md: 14px !default;
$fs-sm: 12px !default;

$breakpoints: (
  small: $mq-sm,
  medium: $mq-md,
  large: $mq-lg
);

$typi: (
  null: ($fs-base, 1.618rem),
  small: ($fs-sm, 1.618rem),
  medium: ($fs-md, 1.618rem),
  large: ($fs-lg, 1.618rem),
);
$h1-map: (
  null: (ms(3) 0.75),
  small: (ms(3,$fs-sm), 0.75),
  medium: (ms(3,$fs-md), 0.75),
  large: (ms(3,$fs-lg), 0.75)
);
$h2-map: (
  null: (ms(2), 1.5),
  small: (ms(2,$fs-sm)),
  medium: (ms(2,$fs-md)),
  large: (ms(2,$fs-lg), 1.5),
);
$h3-map: (
  null: (ms(1), 2),
  small: (ms(1,$fs-sm)),
  medium: (ms(1,$fs-sm)),
  large: (ms(1,$fs-lg), 2)
);
$h4-map: (
  null: (ms(0)), 
  small: (ms(0,$fs-sm)),
  medium: (ms(0,$fs-md)),
  large: (ms(0,$fs-lg)),
);
$h5-map: (
  null: (ms(-1)),
  small: (ms(-1,$fs-sm)),
  medium: (ms(-1,$fs-md)),
  large: (ms(-1, $fs-lg)),
);
$h6-map: (
  null: (ms(-2)),
  small: (ms(2,$fs-sm)),
  medium: (ms(2,$fs-md)),
  large: (ms(-2,$fs-lg)),
);

// border radius
$radius: 3px;

// navbar
$content-width: 1000px !default;
$breakpoint: $mq-md !default;
$nav-height: 4.5rem !default;
$nav-toggle-speed: 100ms;

// navbar dark version
$nav-background: #353535 !default;
$nav-background-opacity: 1 !default;
$nav-font-color: #ffffff !default;
$nav-anchor-padding: 0 20px !default;
$nav-dropdowns-bg: #f7f7f7 !default;
$link-hover-background: lighten($nav-background, 7%) !default;
$link-hover-color: #ffffff !default; 
$link-hover-opacity: 1 !default;

// forms
$form-element-border: #efefef !default;
$form-element-background: #f7f7f7 !default;
$form-submit-hbg: #3498db !default;
$form-submit-hc: #ffffff !default;
$fieldset-border: none !default;
$fieldset-bg: none !default;
$fieldset-padding: 0 !default;
$fieldset-margin: 0 !default;
$fieldset-color: inherit !default;

// default buttons
$buttons: 	(default #cccccc),
			(primary #3498db),
			(secondary #2ecc71),
			(danger #e74c3c) !default;

$btn-color: #fff;

$btn-class-sm: 'btn-sm' !default;
$btn-class-md: 'btn-md' !default;
$btn-class-lg: 'btn-lg' !default;

$btn-lx: (
  null: (ms(3),1.618),
  small: (ms(3,$fs-sm),1.618),
  medium: (ms(3,$fs-sm),1.618),
  large: (ms(3,$fs-lg),1.618)
) !default;
$btn-lg: (
  null: (ms(2),1.618),
  small: (ms(2,$fs-sm),1.618),
  medium: (ms(2,$fs-sm),1.618),
  large: (ms(2,$fs-lg),1.618)
) !default;
$btn-md: (
  null: (ms(1), 1.618), 
  small: (ms(1,$fs-sm), 1.618),
  medium: (ms(1,$fs-md), 1.618),
  large: (ms(1,$fs-lg), 1.618),
) !default;
$btn-sm: (
  null: (ms(0), 1.618),
  small: (ms(0,$fs-sm), 1.618),
  medium: (ms(0,$fs-md), 1.618),
  large: (ms(0, $fs-lg), 1.618),
) !default;

// menus
$menu-bg: #f7f7f7 !default;
$menu-hover-bg: #1B85F5 !default;
$menu-border: 1px solid #efefef !default;
$menu-border-radius: 3px !default;
$menu-color: #222222 !default;
$menu-hover-color: #ffffff !default;
$menu-anchor-padding: ms(-2) ms(0) !default;

// tables
$table-border-color: 1px solid #efefef !default;
$table-row-border-color: 1px solid #efefef !default;
$table-cell-padding: ms(-2) ms(0) !default;
$table-even-row-bg: #f7f7f7 !default;
$table-header-bg: #353535 !default;
$table-header-color: #ffffff !default;
$table-td-align: left !default;
$table-td-mobile-align: left !default;