/**
 * html [class*="viewport"] element
 * sets any element that has viewport to automatically take up the full width
 * and height of the viewport
 */
[class*="viewport"] {
	min-height: 100vh;
	min-width: 100vw;
	display: flex;
}
/* center content vertically in the viewport */
[class*="-vc"] {
	justify-content: center;
}
/* center cotent horizontall in the viewport */
[class*="-hc"] {
	align-items: center;	
}